"use client"

import { useState, useEffect } from 'react';
import Header from "@/components/Header";
import TiptapEditor from "@/components/TiptapEditor";
import { createNotice } from '@/app/actions';

interface Inquiry {
  id: number;
  name: string;
  company: string;
  title: string;
  email: string;
  message: string;
  createdAt: string;
  updatedAt: string;
}

interface Notice {
  id: number;
  title: string;
  content: string | null;
  createdAt: string;
  updatedAt: string;
}

export default function AdminPage() {
  const [activeTab, setActiveTab] = useState(0);
  const [inquiries, setInquiries] = useState<Inquiry[]>([]);
  const [notices, setNotices] = useState<Notice[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  // 공지사항 작성 관련 상태
  const [isCreatingNotice, setIsCreatingNotice] = useState(false);
  const [noticeTitle, setNoticeTitle] = useState('');
  const [noticeContent, setNoticeContent] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      // 문의 데이터 가져오기
      const inquiriesResponse = await fetch('/api/inquiries');
      if (inquiriesResponse.ok) {
        const inquiriesData = await inquiriesResponse.json();
        setInquiries(inquiriesData);
      }

      // 공지사항 데이터 가져오기
      const noticesResponse = await fetch('/api/notices');
      if (noticesResponse.ok) {
        const noticesData = await noticesResponse.json();
        setNotices(noticesData);
      }
    } catch (error) {
      setError('데이터를 불러오는 중 오류가 발생했습니다.');
    } finally {
      setLoading(false);
    }
  };

  const handleCreateNotice = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);

    const formData = new FormData();
    formData.set('title', noticeTitle);
    formData.set('content', noticeContent);

    const result = await createNotice(formData);
    
    setIsSubmitting(false);

    if (result.success) {
      setNoticeTitle('');
      setNoticeContent('');
      setIsCreatingNotice(false);
      fetchData(); // 데이터 새로고침
    } else {
      alert(result.error || '저장에 실패했습니다.');
    }
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleString('ko-KR');
  };

  if (loading) {
    return (
      <div className="min-h-screen flex flex-col">
        <Header />
        <main className="flex-grow container mx-auto px-4 py-16">
          <div className="max-w-6xl mx-auto">
            <div className="text-center">로딩 중...</div>
          </div>
        </main>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen flex flex-col">
        <Header />
        <main className="flex-grow container mx-auto px-4 py-16">
          <div className="max-w-6xl mx-auto">
            <div className="text-center text-red-600">{error}</div>
          </div>
        </main>
      </div>
    );
  }

  return (
    <div className="min-h-screen flex flex-col">
      <Header />
      <main className="flex-grow container mx-auto px-4 py-16">
        <div className="max-w-6xl mx-auto">
          <h1 className="text-3xl font-bold mb-8">관리자 페이지</h1>
          
          {/* 탭 메뉴 */}
          <div className="flex gap-4 mb-8">
            <button
              onClick={() => setActiveTab(0)}
              className={`px-6 py-3 rounded-lg font-semibold transition-colors ${
                activeTab === 0 
                  ? 'bg-[#04AAAB] text-white' 
                  : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
              }`}
            >
              문의 관리 ({inquiries.length})
            </button>
            <button
              onClick={() => setActiveTab(1)}
              className={`px-6 py-3 rounded-lg font-semibold transition-colors ${
                activeTab === 1 
                  ? 'bg-[#04AAAB] text-white' 
                  : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
              }`}
            >
              공지사항 관리 ({notices.length})
            </button>
          </div>

          {/* 문의 관리 탭 */}
          {activeTab === 0 && (
            <div className="bg-white rounded-lg shadow-md p-6">
              <div className="flex justify-between items-center mb-6">
                <h2 className="text-xl font-semibold">문의 목록</h2>
                <span className="text-gray-600">총 {inquiries.length}건</span>
              </div>

              {inquiries.length === 0 ? (
                <div className="text-center py-8 text-gray-500">
                  아직 문의가 없습니다.
                </div>
              ) : (
                <div className="space-y-4">
                  {inquiries.map((inquiry) => (
                    <div key={inquiry.id} className="border rounded-lg p-4 hover:shadow-md transition-shadow">
                      <div className="grid md:grid-cols-2 gap-4 mb-4">
                        <div>
                          <span className="font-semibold text-gray-700">이름:</span>
                          <span className="ml-2">{inquiry.name}</span>
                        </div>
                        <div>
                          <span className="font-semibold text-gray-700">회사:</span>
                          <span className="ml-2">{inquiry.company}</span>
                        </div>
                        <div>
                          <span className="font-semibold text-gray-700">직책:</span>
                          <span className="ml-2">{inquiry.title}</span>
                        </div>
                        <div>
                          <span className="font-semibold text-gray-700">이메일:</span>
                          <span className="ml-2">{inquiry.email}</span>
                        </div>
                      </div>
                      
                      <div className="mb-4">
                        <span className="font-semibold text-gray-700">문의 내용:</span>
                        <div className="mt-2 p-3 bg-gray-50 rounded-md">
                          <pre className="whitespace-pre-wrap text-sm">{inquiry.message}</pre>
                        </div>
                      </div>
                      
                      <div className="text-sm text-gray-500">
                        접수일: {formatDate(inquiry.createdAt)}
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          )}

          {/* 공지사항 관리 탭 */}
          {activeTab === 1 && (
            <div className="bg-white rounded-lg shadow-md p-6">
              <div className="flex justify-between items-center mb-6">
                <h2 className="text-xl font-semibold">공지사항 관리</h2>
                <button
                  onClick={() => setIsCreatingNotice(true)}
                  className="bg-[#04AAAB] text-white font-semibold px-4 py-2 rounded-md hover:bg-[#038A8A] transition-colors"
                >
                  새 공지사항 작성
                </button>
              </div>

              {/* 공지사항 작성 폼 */}
              {isCreatingNotice && (
                <div className="mb-8 p-6 bg-gray-50 rounded-lg">
                  <h3 className="text-lg font-semibold mb-4">공지사항 작성</h3>
                  <form onSubmit={handleCreateNotice} className="space-y-4">
                    <div>
                      <label htmlFor="notice-title" className="block text-sm font-semibold text-gray-700 mb-2">
                        제목
                      </label>
                      <input
                        type="text"
                        id="notice-title"
                        value={noticeTitle}
                        onChange={(e) => setNoticeTitle(e.target.value)}
                        className="w-full border rounded-md py-2 px-3 text-gray-700 focus:outline-none focus:ring-2 focus:ring-[#04AAAB]"
                        required
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-semibold text-gray-700 mb-2">
                        내용
                      </label>
                      <div className="h-96">
                        <TiptapEditor content={noticeContent} onChange={setNoticeContent} />
                      </div>
                    </div>
                    <div className="flex justify-end gap-4">
                      <button
                        type="button"
                        onClick={() => {
                          setIsCreatingNotice(false);
                          setNoticeTitle('');
                          setNoticeContent('');
                        }}
                        className="px-4 py-2 bg-gray-200 text-gray-800 rounded-md hover:bg-gray-300"
                        disabled={isSubmitting}
                      >
                        취소
                      </button>
                      <button
                        type="submit"
                        className="px-4 py-2 bg-[#04AAAB] text-white rounded-md hover:bg-[#038A8A] disabled:bg-gray-400"
                        disabled={isSubmitting}
                      >
                        {isSubmitting ? '저장 중...' : '저장'}
                      </button>
                    </div>
                  </form>
                </div>
              )}

              {/* 공지사항 목록 */}
              {notices.length === 0 ? (
                <div className="text-center py-8 text-gray-500">
                  아직 공지사항이 없습니다.
                </div>
              ) : (
                <div className="space-y-4">
                  {notices.map((notice) => (
                    <div key={notice.id} className="border rounded-lg p-4 hover:shadow-md transition-shadow">
                      <div className="flex justify-between items-start mb-2">
                        <h3 className="font-semibold text-lg text-gray-800">{notice.title}</h3>
                        <span className="text-sm text-gray-500">
                          {formatDate(notice.createdAt)}
                        </span>
                      </div>
                      <div className="text-sm text-gray-600 mb-2">
                        <div 
                          className="line-clamp-3" 
                          dangerouslySetInnerHTML={{ __html: notice.content || '' }}
                        />
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          )}
        </div>
      </main>
    </div>
  );
} 